/* eslint-disable vue/no-duplicate-attributes */
<template>
  <div class="buy">
    <navgation-bar :pageName="'立即购买'" @click="onBackClick"></navgation-bar>
    <!-- 内容区 -->
    <div class="buy-content">
      <!-- 购买的商品 -->
      <div class="buy-content-goods">
        <!-- 商品图片 -->
        <img class="buy-content-goods-img" :src="goodsData.img" />
        <!-- 商品详情 -->
        <div class="buy-content-goods-desc">
          <p class="goods-desc-name">{{goodsData.name}}</p>
          <p class="goods-desc-price">￥{{goodsData.price|priceValue}}</p>
        </div>
      </div>
      <!-- 支付方式 -->
      <ul class="buy-methods">
        <li v-for="(item,index) in paymentDatas" :key="index" @click="onPaymentItemClick(item)">
          <!-- 支付方式图标 -->
          <img class="method-icon" :src="item.icon" alt />
          <!-- 描述 -->
          <div class="method-desc">
            <!-- 支付方式名称 -->
            <p class="method-desc-name">{{item.name}}</p>
            <!-- 支付方式描述 -->
            <p class="method-desc-desc">{{item.desc}}</p>
          </div>
          <!-- 选择支付方式的标记 -->
          <img class="method-desc-check" :src="getCheckIcon(selectPayment.id===item.id)" alt />
        </li>
      </ul>
    </div>
    <!-- 立即购买 -->
    <div class="pay-page-commit">立即购买</div>
  </div>
</template>
<script>
import NavgationBar from '../components/currency/NavgationBar.vue'
export default {
  components: {
    NavgationBar
  },
  data() {
    return {
      goodsData: {},
      paymentDatas: [
        { id: '1', name: '支付宝支付', desc: '亿万用户都在用,安全可信托', icon: require('../assets/img/alipay.svg') },
        { id: '2', name: '微信支付', desc: '亿万用户选择,更快更安全', icon: require('../assets/weichat.png') }
      ],
      selectPayment: {}
    }
  },
  created() {
    this.selectPayment = this.paymentDatas[0]
    // 获取路由的参数
    this.goodsData = this.$route.params.data
  },
  methods: {
    onBackClick() {
      this.$router.go(-1)
    },
    onPaymentItemClick() { },
    getCheckIcon(isCheck) {
      return isCheck ? require('../assets/img/check.svg') : require('../assets/img/no-check.svg')
    }
  }
}
</script>
<style lang="less" scoped>
.buy {
  position: absolute;
  padding-top: 90/20rem;
  height: 100%;
  width: 100%;
  background-color: #eee;
  .buy-content {
    overflow: hidden;
    height: 100%;
    // 商品
    .buy-content-goods {
      background-color: #fff;
      padding: 0.3rem;
      display: flex;
      border-bottom: 1px solid #ccc;
      border-top: 1px solid #ccc;
      margin-top: 0.3rem;
      .buy-content-goods-img {
        width: 7rem;
        height: 7rem;
      }
      .buy-content-goods-desc {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 0 0.3rem;
        .goods-desc-name {
          font-size: 1.2rem;
          line-height: 1.8rem;
        }
        .goods-desc-price {
          font-size: 1.2rem;
          color: #f10215;
          font-weight: 500;
        }
      }
    }
    // 支付方式
    .buy-methods {
      margin-top: 0.8rem;
      li {
        display: flex;
        background-color: #fff;
        border-bottom: 1px solid #ccc;
        padding: 0.3rem;
        align-items: center;
        .method-icon {
          height: 2rem;
        }
        .method-desc {
          margin-left: 0.8rem;
          flex-grow: 1;
          .method-desc-name {
            font-size: 1rem;
          }
          .method-desc-desc {
            margin-top: 0.3rem;
            color: #555;
          }
        }
        .method-desc-check {
          width: 1.2rem;
          height: 1.2rem;
        }
      }
    }
  }
}
.pay-page-commit {
  position: absolute;
  bottom: 6rem;
  width: 90%;
  height: 76/20rem;
  line-height: 76/20rem;
  text-align: center;
  color: #fff;
  font-size: 1.2rem;
  margin-left: 5%;
  background-color: #f10215;
  border-radius: 1.6rem;
}
</style>
