<template>
  <div ref="goods" @scroll="onScrollChange" class="goods goods-waterfall">
    <div :class="layoutClass">
      <div
        :class="layoutItemClass"
        v-for="(item,index) in sortGoodsDate"
        :key="index"
        @click="onClickItem(item)"
      >
        <!-- 图片 -->
        <img :src="item.img" class="goods-item-img" alt />
        <!-- desc -->
        <div class="goods-item-desc">
          <!-- 是否为直营 -->
          <direct v-if="item.isDirect"></direct>
          <!-- 是否有货 -->
          <no-have v-if="item.isHave"></no-have>
          <span class="goods-item-name">{{item.name}}</span>
          <div class="goods-item-desc-data">
            <p class="price">￥{{item.price|priceValue}}</p>
            <p class="volume">销量: {{item.volume}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Direct from './Direct.vue'
import NoHave from './NoHave.vue'
export default {
  props: {
    layoutType: {
      type: String
    },
    // 排序规则1.默认，1-2价格，
    sort: {
      type: String,
      default: '1'
    }
  },
  data() {
    return {
      goodsList: [],
      // 垂直列表的展示形式
      layoutClass: 'goods-list',
      layoutItemClass: 'goods-list-item',
      sortGoodsDate: [],
      scrollTopValue: 0
    }
  },
  watch: {
    layoutType() {
      this.initLayout()
    },
    sort() {
      this.setSortGoodsData()
    }
  },
  components: {
    Direct,
    NoHave
  },
  created() {
    this.initData()
    this.initLayout()
    this.onScrollChange()
  },
  activated() {
    this.$refs.goods.scrollTop = this.scrollTopValue
  },
  methods: {
    onClickItem(item) {
      if (!item.isHave) {
        alert('商品没有库存了!!')
        return
      }
      this.$router.push({ name: 'goodsdetail', params: { goods: item, routerType: 'push' } })
    },
    setSortGoodsData() {
      switch (this.sort) {
        // 默认
        case '1':
          this.sortGoodsDate = this.goodsList.slice(0)
          break
        // 几个由高到低
        case '1-2':
          this.getSortGoodsDataFromKey('price')
          break
        // 销量由高到低
        case '1-3':
          this.getSortGoodsDataFromKey('volume')
          break
        // 有货优先
        case '2':
          this.getSortGoodsDataFromKey('isHave')

          break
        // 直营优先
        case '3':
          this.getSortGoodsDataFromKey('isDirect')
          break
      }
    },
    getSortGoodsDataFromKey(key) {
      this.sortGoodsDate.sort((goods1, goods2) => {
        let v1 = goods1[key]
        let v2 = goods2[key]
        if (typeof v1 === 'boolean') {
          if (v1) {
            return -1
          }
          if (v2) {
            return 1
          }
          return 0
          // eslint-disable-next-line no-unreachable
          if (!parseFloat(v1) >= parseFloat(v2)) {
            return -1
          }
          return 1
        }
      })
    },
    initLayout() {
      switch (this.layoutType) {
        case '1':
          this.layoutClass = 'goods-list'
          this.layoutItemClass = 'goods-list-item'
          break
        case '2':
          this.layoutClass = ''
          this.layoutItemClass = 'goods-item'
          break
      }
    },
    initData() {
      this.$http.get('/data.json').then(data => {
        this.goodsList = data.data.goods
        this.setSortGoodsData()
      })
    },
    onScrollChange() {
      var that = this
      document.addEventListener('scroll', function () {
        that.scrollTopValue = this.documentElement.scrollTop
      })
    }
  }
}
</script>
<style lang="less" scoped>
.goods {
  position: absolute;
  background-color: #eee;
  padding: 0.2rem;
  .goods-item {
    background-color: #fff;
    margin: 0.2rem;
    height: 360/20rem;
    .goods-item-desc {
      width: 100%;
      .goods-item-name {
        font-size: 1rem;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        box-orient: vertical;
        display: -webkit-box;
      }
      .goods-item-desc-data {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 0.2rem;
        .price {
          font-size: 1rem;
          color: #f10215;
          font-weight: 500;
        }
        .volume {
          font-size: 0.8rem;
          color: #555;
        }
      }
    }
  }
  .goods-list {
    .goods-list-item {
      display: flex;
      border-bottom: 1px solid #ccc;
      background-color: #fff;
      margin-bottom: 0.3rem;
      .goods-item-img {
        width: 7rem;
        height: 8rem;
        margin-right: 0.3rem;
      }
      .goods-item-desc {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 0.2rem;
      }
    }
  }
}
.goods-waterfall {
  position: relative;
  margin: 0.2rem;
  .goods-item {
    float: left;
    width: 48%;
    border-radius: 0.1rem;
    .goods-item-img {
      width: 100%;
    }
  }
}
</style>
