<template>
  <swiper id="swiper" :options="swiperOption" ref="mySwiper">
    <!-- slides -->
    <swiper-slide v-for="(item, index) in swiperImgs" :key="index">
      <img class="swiper-slide-img" :src="item" alt srcset :style="{height: height}" />
    </swiper-slide>
    <!-- Optional controls -->
    <template v-slot:pagination>
      <div class="swiper-pagination"></div>
    </template>
  </swiper>
</template>
<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  props: {
    height: {
      type: String,
      default: 'auto'
    },
    //   图片
    swiperImgs: {
      type: Array,
      required: true,
      default: function () {
        return []
      }
    },
    paginationType: {
      type: String,
      default: '1'
    }
  },
  components: {
    swiper,
    swiperSlide
  },
  created() {
    this.initPaginationLatout()
  },
  methods: {
    initPaginationLatout() {
      switch (this.paginationType) {
        case '1':
          this.swiperOption.pagination = {
            el: '.swiper-pagination',
            //   分页器样式
            type: 'bullets',
            // 分页器内元素的类名
            bulletClass: 'custom-bullet-class'
          }
          break
        case '2':
          this.swiperOption.pagination = {
            el: '.swiper-pagination',
            //   分页器数字
            type: 'fraction',
            // 分页器内元素的类名
            bulletClass: 'custom-fraction-class'
          }
          break
      }
    }
  },
  data() {
    return {
      // swiper配置
      swiperOption: {
        autoheight: true,
        // 分页器
        pagination: {
          el: '.swiper-pagination',
          //   分页器样式
          type: 'bullets'

        }
      }
    }
  }
}
</script>
<style lang="less">
.swiper-container {
  .swiper-pagination {
    height: 2rem;
    .custom-bullet-class {
      display: inline-block;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      margin: 0 3px;
      border: 1px solid #fff;
    }
    .swiper-pagination-bullet-active {
      background-color: #fff;
    }
  }
}
.swiper-pagination-fraction {
  position: absolute;
  width: 6rem;
  left: 20rem;
  height: 2rem;
  color: #fff;
  font-size: 1.2rem;
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 2rem 0 0 2rem;
}
</style>
