<template>
  <div class="tool-bar">
    <!-- tab按钮 -->
    <div
      @click="changeFragment(item,index)"
      class="tool-bar-item"
      v-for="(item,index) in toolBarData"
      :key="index"
    >
      <img :src="index===selectItemIndex?item.hIcon:item.nIcon" class="tool-bar-item-img" />
      <p
        :class="{'tool-bar-item-name-h':index===selectItemIndex}"
        class="tool-bar-item-name"
      >{{item.name}}</p>
    </div>
  </div>
</template>
<script>
export default {
  computed: {
    selectItemIndex() {
      return this.$store.state.component.selectItemIndex
    }
  },
  data() {
    return {
      toolBarData: [
        {
          // 默认状态下的图片
          nIcon: require('../assets/img/home-n.svg'),
          // 高亮状态下的图片
          hIcon: require('../assets/img/home-h.svg'),
          // 名称
          name: '首页',
          // 组件名称
          componentName: 'home'
        },
        {
          nIcon: require('../assets/img/shopping-n.svg'),
          hIcon: require('../assets/img/shopping-h.svg'),
          name: '购物车',
          componentName: 'shopping'
        },
        {
          nIcon: require('../assets/img/my-n.svg'),
          hIcon: require('../assets/img/my-h.svg'),
          name: '我的',
          componentName: 'my'
        }
      ]
    }
  },
  methods: {
    changeFragment(item, index) {
      let obj = {
        selectItemIndex: index,
        componentName: item.componentName
      }
      this.$emit('changeFragment', obj)
    },
    // 指定加载的页面组件
    pushFragment(index) {
      this.changeFragment(this.toolBarData[index], index)
    }
  }
}
</script>
<style lang="less" scoped>
@mainColor: #d81e06;
// 分割线颜色
@lineColor: #e5e5e5;
.tool-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-around;
  background-color: #fff;
  box-shadow: 0 0 16px 0 rgba(0, 0, 0, 0.2);
  height: 76/20rem;
  width: 100%;
  .tool-bar-item {
    text-align: center;
    padding: 0.5rem 0;
    img {
      width: 30/20rem;
    }
  }
  .tool-bar-item-name-h {
    color: @mainColor;
  }
}
</style>
