import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    component: {
      componentName: 'home',
      selectItemIndex: 0
    },
    shoppingDatas: []
  },
  mutations: {
    // 修改component
    changeFragment(state, obj) {
      state.component = obj
    },
    // 添加商品到购物车数据源
    addShoppingDatas(state, goods) {
      // 判断购物车中是否包含了改商品，如果包含了把这个商品的number+1，如果不包含在添加到购物车中
      const isExists = state.shoppingDatas.some(item => {
        if (item.img === goods.img) {
          item.number = item.number + 1
          return true
        }
      })
      if (!isExists) {
        Vue.set(goods, 'isCheck', false)
        Vue.set(goods, 'number', 1)
        state.shoppingDatas.push(goods)
      }
    },
    changeSoppingDataNumber(state, data) {
      state.shoppingDatas[data.index].number = data.number
    }
  },
  actions: {},
  modules: {}
})
