<template>
  <div class="goods-list-page">
    <navgation-bar @onLeftClick="onBackClick" :pageName="'商品列表'">
      <template v-slot:nav-right>
        <img style="width:1.6rem;margin:0.6rem" :src="layoutType.icon" @click="onChangeTypeClick" />
      </template>
    </navgation-bar>
    <div class="goods-list-content">
      <goods-options @optionsChange="onGoodsOptionsChange"></goods-options>
      <goods :sort="sortType" :layoutType="layoutType.type"></goods>
    </div>
  </div>
</template>
<script>
import NavgationBar from '../components/currency/NavgationBar.vue'
import GoodsOptions from '../components/goods/GoodsOptions.vue'
import Goods from '../components/goods/Goods.vue'
export default {
  data() {
    return {
      layoutTypeDatas: [
        // 垂直布局
        {
          type: '1',
          icon: '/img/list-type.svg'
        },
        {
          // 网格布局
          type: '2',
          icon: '/img/grid-type.svg'
        }
      ],
      layoutType: {
        type: '1',
        icon: '/img/list-type.svg'
      },
      sortType: '1'
    }
  },
  components: {
    NavgationBar,
    GoodsOptions,
    Goods
  },
  methods: {
    //   点击后退
    onBackClick() {
      this.$router.go(-1)
    },
    onChangeTypeClick() {
      if (this.layoutType.type === '1') {
        this.layoutType = this.layoutTypeDatas[1]
      } else {
        this.layoutType = this.layoutTypeDatas[0]
      }
    },
    onGoodsOptionsChange(sortType) {
      this.sortType = sortType
    }
  }
}
</script>
<style lang="less" scoped>
.goods-list-page {
  width: 100%;
  height: 100%;
  background-color: #eee;
  display: flex;
  flex-direction: column;
  margin: 3rem 0;
  .goods-list-content {
    height: 100%;
    display: flex;
    flex-direction: column;
  }
}
</style>
