<template>
  <div :style="navBarStyle" class="nav-bar z-index-max" :class="{'line-bottom':pageName}">
    <!-- 左 -->
    <div class="left" @click="$emit('onLeftClick')">
      <!-- 默认展示 -->
      <img v-if="isShowBack" src="../../assets/img/back.svg" />
      <!-- 具名插槽 -->
      <slot name="nav-left"></slot>
    </div>
    <!-- 中 -->
    <div class="center">
      <!-- 默认状态 -->
      <span v-if="pageName!==''" class="page-title">{{pageName}}</span>
      <!-- 具名插槽 -->
      <slot name="nav-center"></slot>
    </div>
    <!-- 右 -->
    <div class="right">
      <!-- 具名插槽 -->
      <slot name="nav-right"></slot>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    pageName: {
      type: String,
      default: ''
    },
    // 是否展示后退按钮
    isShowBack: {
      type: Boolean,
      default: true
    },
    navBarStyle: {
      type: Object,
      default: function () {
        return {
          background: 'rgba(244,23,68,0)'
        }
      }
    }
  },
  data() {
    return {

    }
  }
}
</script>
<style lang="less" scoped>
.nav-bar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 76/20rem;
  line-height: 3rem;
  display: flex;
  justify-content: space-between;
  color: #fff;
  background-color: #fff;
  .left {
    display: flex;
    height: 100%;
    width: 2rem;
    padding: 0 0.3rem;
    img {
      width: 100%;
    }
  }
  .center {
    display: flex;
    width: 76%;
    height: 100%;
    .page-title {
      width: 100%;
      text-align: center;
      font-size: 1.2rem;
      color: #000;
    }
    p {
      font-size: 1.2rem;
    }
  }
  .right {
    display: flex;
    width: 2rem;
    img {
      width: 100%;
    }
  }
}
.line-bottom {
  border-bottom: 1px solid #ccc;
}
</style>
