<template>
  <div class="goods-detail">
    <navgation-bar :isShowBack="false" @click="onBackClick" :navBarStyle="navBarStyle">
      <template v-slot:nav-left>
        <div class="goods-detail-nav-left">
          <img
            @click="onBackClick"
            class="back-black"
            style="width:2rem;margin-top:10px"
            src="../assets/img/back-2.svg"
            alt
          />
          <img
            @click="onBackClick"
            class="back-white"
            style="width:2rem;margin-top:10px"
            src="../assets/img/back-white.svg"
            alt
          />
        </div>
      </template>
      <template v-slot:nav-center>
        <div style="font-size:1.2rem;text-align:center;width: 100%;">商品详情</div>
      </template>
    </navgation-bar>

    <div class="goods-detail-content">
      <my-swiper :paginationType="'2'" :height="'27rem'" :swiperImgs="goodsData.swiperImgs"></my-swiper>

      <!-- 内容 -->
      <div class="goods-detail-content-desc">
        <div class="desc-item">
          <p class="price">{{goodsData.price|priceValue}}</p>
          <p class="desc-name">
            <!-- 直营 -->
            <direct v-if="goodsData.isDirect"></direct>
            {{goodsData.name}}
          </p>
        </div>
        <div class="desc-item">
          <p class="desc-item-select">
            已选
            <span class="single-row-text"></span>
          </p>
          <!-- 附加服务 -->
          <div class="desc-item-support">
            <ul>
              <li v-for="(item,index) in attachDatas" :key="index">
                <img src="../assets/img/support.svg" alt />
                <span>{{item}}</span>
              </li>
            </ul>
          </div>
        </div>

        <!--  -->
        <div class="content-desc-detail">
          <img v-for="(item,index) in goodsData.detailImgs" :key="index" :src="item" />
        </div>
      </div>

      <!-- 加入购物车、立即购买 -->
      <div class="goods-detail-buy">
        <div @click="onAddGoodsClick" class="add-cart">加入购物车</div>
        <div @click="onBuyClick" class="buy-at-once">立即购买</div>
      </div>
    </div>
  </div>
</template>
<script>
import Direct from '../components/goods/Direct.vue'

import NavgationBar from '../components/currency/NavgationBar.vue'
import MySwiper from '../components/swiper/MySwiper.vue'
export default {
  components: {
    NavgationBar,
    MySwiper,
    Direct
  },
  created() {
    this.goodsData = this.$route.params.goods
  },
  data() {
    return {
      navBarStyle: {
        backgroundColor: 'transparent',
        goodsData: {}
      },
      // 附加服务
      attachDatas: [
        '可配送海外',
        '京东发货&售后',
        '京准达',
        '211限时达'
      ]
    }
  },
  methods: {
    onBackClick() {
      this.$router.go(-1)
    },
    // 添加到购物车
    onAddGoodsClick() {
      alert('添加成功')
      // 修改组件的名称，改为购物车的
      this.$store.commit('changeFragment', {
        componentName: 'shopping',
        selectItemIndex: 1
      })
      this.$store.commit('addShoppingDatas', this.goodsData)
      this.$router.push({
        name: 'home',
        params: {
          routerType: 'push'
        }
      })
    },
    onBuyClick() {
      this.$router.push({
        name: 'buy',
        params: {
          routerType: 'push',
          data: this.goodsData
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.goods-detail {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column;
  .goods-detail-nav-left {
    position: relative;
    width: 100%;
    display: flex;
    img {
      position: absolute;
      top: 0px;
      left: 0.3rem;
      align-self: center;
    }
  }
  .goods-detail-content {
    overflow: hidden;
    overflow-y: auto;
    height: 100%;
    .goods-detail-content-desc {
      width: 100%;
      background-color: #eee;
      box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
      .desc-item {
        background-color: #fff;
        padding: 0.3rem;
        margin-bottom: 0.3rem;
        // 价格
        .price {
          font-size: 1.2rem;
          color: #f10215;
          font-weight: 500;
        }
        // 商品名称
        .desc-name {
          margin-top: 0.3rem;
          font-size: 1.1rem;
          font-weight: 500;
          line-height: 1.6rem;
        }
        // 已选商品
        .desc-item-select {
          color: #555;
          height: 1.6rem;
          display: flex;
          align-items: center;
          border-bottom: 1px solid #ccc;
          span {
            font-weight: 500;
            display: inline-block;
          }
        }
        // 支持
        .desc-item-support {
          display: flex;
          height: 3rem;
          flex-wrap: wrap;
          ul {
            width: 100%;
            li {
              display: flex;
              align-items: center;
              padding: 0.3rem 0;
              margin-right: 0.6rem;
              width: 33.33%;
              float: left;
              img {
                width: 1rem;
                margin-right: 0.3rem;
              }
            }
          }
        }
      }
      .content-desc-detail {
        width: 100%;
        img {
          width: 100%;
        }
      }
    }
    .goods-detail-buy {
      position: fixed;
      bottom: 0;
      left: 0;
      display: flex;
      justify-content: flex-end;
      width: 100%;
      background-color: #fff;
      margin-top: 0.3rem;
      height: 3rem;
      text-align: right;
      line-height: 3rem;
      div {
        display: inline-block;
        width: 8rem;
        font-size: 1.2rem;
        text-align: center;
        color: #fff;
      }
      .add-cart {
        background-color: #f10215;
      }
      .buy-at-once {
        background-color: darkgoldenrod;
      }
    }
  }
}
.single-row-text {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline-block;
}
</style>
