<template>
  <div>
    <!-- 动态组件 -->
    <component :is="viewComponent"></component>
    <tool-bar ref="toolbar" @changeFragment="changeFragment"></tool-bar>
  </div>
</template>
<script>
import ToolBar from '../components/ToolBar.vue'
export default {
  components: {
    'tool-bar': ToolBar,
    'home': () => import('../components/Home.vue'),
    'my': () => import('../components/My.vue'),
    'shopping': () => import('../components/Sopping.vue')
  },
  computed: {
    viewComponent() {
      return this.$store.state.component.componentName
    }
  },

  data() {
    return {
    }
  },
  activated() {
    this.pushFragment()
  },
  methods: {
    changeFragment(obj) {
      this.$store.commit('changeFragment', obj)
    }

  }
}
</script>
