<template>
  <div id="app">
    <!-- 路由动画 在进入新页面的时候，页面从右向左滑动过来
            在返回原页面的时候，页面从左往右滑动过来
    -->
    <transition :name="transitionName">
      <!-- 所有通过router-view加载的页面都要缓存 -->
      <keep-alive :include="virtualTaskStack">
        <router-view />
      </keep-alive>
    </transition>
  </div>
</template>
<script>
export default {
  data() {
    return {
      transitionName: 'fold-left',
      virtualTaskStack: [
        'imooc'
      ]
    }
  },
  watch: {
    '$route'(to, from) {
      const routerType = to.params.routerType
      if (routerType === 'push') {
        // 跳转页面
        this.virtualTaskStack.push(to.name)
        this.transitionName = 'fold-left'
      } else {
        // 后退页面
        this.virtualTaskStack.pop()
        this.transitionName = 'fold-right'
      }
    }
  }
}
</script>
<style lang="less" scoped>
#app {
  width: 100%;
  height: 100%;
  padding-bottom: 76/20rem;
  [class^='fold-left'] {
    position: absolute;
  }
  // 新页面的进入动画
  .fold-left-enter-active {
    animation: fold-left-in 0.4s;
  }
  .fold-left-leave-active {
    animation: fold-left-out 0.4s;
  }
  @keyframes fold-left-in {
    0% {
      transform: translate(100%, 0);
    }
    100% {
      transform: translate(0, 0);
    }
  }
  @keyframes fold-left-out {
    0% {
      transform: translate(0, 0);
    }
    100% {
      transform: translate(-100%, 0);
    }
  }
  .fold-right-enter-active {
    animation: fold-right-out 0.4s;
  }
  .fold-right-leave-active {
    animation: fold-right-in 0.4s;
  }
  @keyframes fold-right-in {
    0% {
      transform: translate(-100%, 0);
    }
    100% {
      transform: translate(0, 0);
    }
  }
  @keyframes fold-right-out {
    0% {
      transform: translate(0, 0);
    }
    100% {
      transform: translate(100%, 0);
    }
  }
}
</style>
