<template>
  <div class="goods-options">
    <ul class="goods-options-list">
      <li class="goods-options-item" v-for="(item,index) in optionsData" :key="index">
        <a class="item-content" href="javascript:;" @click="onOptionsItem(item,index)">
          <span
            class="content-name"
            :class="{'content-name-active':selectedOption.id==item.id}"
          >{{item.name}}</span>
          <span
            :class="[isShow&&selectedOption.id===item.id?'content-caret-open':'content-caret-close']"
            v-if="item.subs.length>0"
            class="content-caret"
          ></span>
        </a>
      </li>
    </ul>
    <transition name="fold-height">
      <!-- 自选项内容 -->
      <div v-if="isShow" class="options-sub-content">
        <ul>
          <li v-for="(item,index) in selectedOption.subs" :key="index">
            <a href="javascript:;" @click="onSubOptionsItemClick(item,index)">
              <span :class="{'active':selectedOption.id==item.id}">{{item.name}}</span>
              <img src="../../assets/img/options-select.svg" alt />
            </a>
          </li>
        </ul>
      </div>
    </transition>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 筛选项
      optionsData: [
        {
          id: '1',
          name: '默认',
          subs: [
            {
              id: '1',
              name: '默认'
            },
            {
              id: '1-2',
              name: '价格由高到低'
            },
            {
              id: '1-3',
              name: '销量由高到低'
            }
          ]
        },
        {
          id: '2',
          name: '有货优先',
          subs: []
        },
        {
          id: '3',
          name: '直营优先',
          subs: []
        }
      ],
      //   选择的筛选项
      selectedOption: {},
      isShow: false
    }
  },
  watch: {
    selectedOption(newV) {
      this.$emit('optionsChange', newV.id)
    }

  },

  created() {
    this.selectedOption = this.optionsData[0]
  },
  methods: {
    onOptionsItem(item, index) {
      if (this.isShow) {
        this.isShow = false
        return ''
      }
      if (item.subs.length > 0 && this.selectedOption.id === item.id) {
        this.isShow = true
      }
      this.selectedOption = item
    },
    onSubOptionsItemClick(item, index) {
      this.selectedOption = item
      this.optionsData.forEach(item => {
        item.subs.forEach(sub => {
          if (sub.id === this.selectedOption.id) {
            item.id = sub.id
            item.name = sub.name
          }
        })
      })
      this.isShow = false
    }
  }
}
</script>
<style lang="less" scoped>
.goods-options {
  display: relative;
  z-index: 9999;
  width: 100%;
  border-bottom: 1px solid #ccc;
  .goods-options-list {
    display: flex;
    width: 100%;
    height: 2.6rem;
    background-color: #fff;
    .goods-options-item {
      height: 100%;
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      .content-caret-open {
        transform: rotate(180deg);
        transition: transform 0.3s;
      }
      .content-caret-close {
        transform: rotate(0deg);
        transition: transform 0.3s;
      }
      .content-name {
        font-size: 1rem;
      }
      .content-name-active {
        color: #f10215;
      }
    }
  }
  .options-sub-content {
    position: absolute;
    width: 100%;
    max-height: 8rem;
    overflow: hidden;
    overflow-y: scroll;
    background-color: #fff;
    ul {
      li {
        a {
          display: flex;
          align-items: center;
          border-top: 1px solid #ccc;
          padding: 0.4rem;
          height: 2.6rem;
          span {
            font-size: 1rem;
            width: 90%;
          }
          .active {
            color: #f10215;
          }
          img {
            width: 1.6rem;
          }
        }
      }
    }
  }
}
// 三角形
.content-caret {
  display: inline-block;
  width: 0;
  height: 0;
  border-top: 8px solid #f10215;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  margin: 0.2rem 0 0 0.2rem;
}

.fold-height-enter-active {
  animation-duration: 0.3s;
  animation-name: fold-height-open;
}
.fold-height-leave-active {
  animation-name: fold-height-close;
  animation-duration: 0.3s;
}
@keyframes fold-height-open {
  0% {
    height: 0;
  }
  100% {
    height: 8rem;
  }
}
@keyframes fold-height-open {
  0% {
    height: 8rem;
  }
  100% {
    height: 0;
  }
}
</style>
